.container, .site-page {
  width: min(100% - 4rem, 80em);
  margin-inline: auto;
}

@keyframes move-in {
  0% {
    transform: translateX(100vw);
  }
  100% {
    transform: translateX(0);
  }
}
.move-in {
  animation: move-in 0.5s ease-in-out forwards;
}

@keyframes move-out {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100vw);
  }
}
.move-out {
  animation: move-out 0.5s ease-in-out forwards;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd,
ul {
  margin: 0;
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  font-family: "Nunito", sans-serif;
  color: #2e2e2e;
  background-color: #e8e8e8;
}

h1 {
  color: #b8c8c8;
}

.site-page {
  margin-block: 5rem;
}
@media (max-width: 60em) {
  .site-page {
    margin-block: 3rem;
  }
}

header {
  position: relative;
  height: 70vh;
  background-color: #102028;
  background-size: cover;
  background-attachment: fixed;
}
header .navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  transition: all 0.6s ease-in;
  z-index: 9999;
}
header .navbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
header .navbar__brand {
  z-index: 99999;
}
header .navbar__brand a {
  text-decoration: none;
}
header .navbar__brand a h1 {
  color: #e8e8e8;
}
header .navbar__menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 2rem;
  list-style: none;
}
@media (max-width: 39.9375em) {
  header .navbar__menu {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    padding: 0;
    height: 100vh;
    width: 100%;
    background-color: #283038;
    flex-direction: column;
    gap: 2.5rem;
    z-index: 9999;
  }
}
header .navbar__menu--link {
  text-decoration: none;
  text-transform: uppercase;
  color: #e8e8e8;
  letter-spacing: 0.1em;
}
header .navbar__btn {
  display: none;
  padding: 0;
  border: none;
  background-color: transparent;
  color: #ffffff;
  cursor: pointer;
  z-index: 99999;
  height: 30px;
  width: 25px;
  position: relative;
}
@media (max-width: 39.9375em) {
  header .navbar__btn {
    display: block;
  }
}
header .navbar__btn--container {
  position: relative;
  width: 100%;
  height: 100%;
}
header .navbar__btn--container span {
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: #ffffff;
  border-radius: 1rem;
  left: 0;
  transition: all 0.2s ease-in-out;
}
header .navbar__btn--container span:nth-child(1) {
  top: 18%;
}
header .navbar__btn--container span:nth-child(2) {
  top: 48%;
}
header .navbar__btn--container span:nth-child(3) {
  top: 78%;
}
header .navbar__btn.close span:nth-child(1) {
  transform: rotate(45deg);
  top: 48%;
}
header .navbar__btn.close span:nth-child(2), header .navbar__btn.close span:nth-child(3) {
  transform: rotate(-45deg);
  top: 48%;
}
header .navbar.sticky {
  background-color: #283038;
  height: 80px;
}
header .banner {
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
}
header .banner__content {
  width: min(100% - 5rem, 500px);
  margin-inline: auto;
}
header .wave {
  position: absolute;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  height: 150px;
}
@media (max-width: 39.9375em) {
  header .wave {
    height: 50px;
  }
}

.gallery__container {
  display: grid;
  grid-template-columns: repeat(4, 300px);
  gap: 1rem;
  place-content: center;
}
@media (max-width: 87.4375em) {
  .gallery__container {
    grid-template-columns: repeat(3, 300px);
  }
}
@media (max-width: 71.8125em) {
  .gallery__container {
    grid-template-columns: repeat(3, 250px);
  }
}
@media (max-width: 60em) {
  .gallery__container {
    grid-template-columns: repeat(2, 300px);
  }
}
@media (max-width: 39.9375em) {
  .gallery__container {
    grid-template-columns: 300px;
  }
}
.gallery__item {
  height: 300px;
  width: 300px;
  background-color: #102028;
  border-radius: 0.25rem;
  overflow: hidden;
}
@media (max-width: 71.8125em) {
  .gallery__item {
    height: 250px;
    width: 250px;
  }
}
@media (max-width: 60em) {
  .gallery__item {
    height: 300px;
    width: 300px;
  }
}
.gallery__item:hover {
  cursor: pointer;
}
.gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.footer {
  color: #e8e8e8;
  background-color: #102028;
}
.footer__content {
  background-color: #283038;
  height: 30vh;
}
.footer__copyright {
  background-color: #b8c8c8;
  color: #283038;
  padding-block: 1rem;
  display: grid;
  place-content: center;
}
.footer__copyright--text {
  font-size: 0.85rem;
  text-align: center;
}